var Vue = require('../lib/vue');
var VueResource = require('../lib/vue-resource');
var Config = require('../config/env');

Vue.use(VueResource);

new Vue({
    el : '.register',
    data : {
        isSend : true,
        codeMsg : "",
        countdown : 5,
        user : {
            name : "12313",
            phone : ""
        }
    },
    methods : {
        postData : function(){
            this.$http.get(Config.api+ '/someUrl').then(function(res){

            },function(res){

            });
        },
        sendCode : function(){
            var self = this;
            function settime() { 
                if (self.countdown == 0) {
                    // obj.removeAttr("disabled");    
                    self.codeMsg = "";
                    self.countdown = 5;
                    return;
                } else {
                    self.codeMsg = "(" + self.countdown + "s)"; 
                    self.countdown--;
                } 
                setTimeout(function() { settime() },1000) //1000
            } 
            settime()

        }
    }
});






